<template>
  <div>
    <MHeader :back="true">首页</MHeader>
    <div class="content">
      <Loading v-if="loading"></Loading>
      <template v-else>
        <Swiper :swiperSlides="sliders"></Swiper>
        <div class="container">
          <h3>热门图书</h3>
          <ul>
            <li v-for="(hot,index) in hotBooks" :key="index">
              <img :src="hot.bookCover" alt="hot.bookName">
              <b>{{hot.bookName}}</b>
            </li>
          </ul>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
  import MHeader from '../base/MHeader.vue';
  import Swiper from '../base/Swiper.vue';
  import Loading from '../base/Loading.vue';
  //import {getSliders, getHotBook} from "../api";
  import {getAll} from "../api";

  export default {
    name: "Home",
    data() {
      return {
        sliders: [],
        hotBooks: [],
        loading: true
      }
    },
    components: {
      MHeader,
      Loading,
      Swiper
    },
    created() {
      // this.getSlider();
      // this.getHot();// 获取异步图书
      this.getData();
    },
    methods: {
      // async getSlider() {
      //   // 给data起别名 对象中的属性名字必须和得到的结果名字一致
      //   //let {data} = await getSliders();
      //   // 将获取的数据放到sliders中
      //   //this.sliders = data;
      //   this.sliders = await getSliders();
      // },
      // async getHot() {
      //   //let {data}=await getHotBook();
      //   //this.hotBooks=data;
      //   this.hotBooks = await getHotBook();
      // }
      async getData() {
        let [sliders, hotBooks] = await getAll();
        this.sliders = sliders;
        this.hotBooks = hotBooks;
        //轮播图和热门图书已经获取完成
        this.loading=false;
      }
    }
  }
</script>

<style scoped lang="less">
  .container {
    width: 90%;
    margin: 0 auto;
    ul {
      display: flex;
      flex-wrap: wrap; // 默认换行
      li {
        width: 50%;
        text-align: center;
        margin: 5px 0;
        img {
          width: 100%;
        }
      }
    }
  }
</style>



